
import { defineComponent } from 'vue';
import { mapActions, mapGetters } from 'vuex';
import BookCartBar from '../components/BookCartBar.vue';
import TheLoader from '../components/TheLoader.vue';
import ButtonBrown from '../ui/ButtonBrown.vue';

interface booksType {
  author: string,
  authorId: string,
  title: string,
  price: number,
  image: string,
  key: string,
  discount: number | null,
  quantityInCart: number
}

export default defineComponent({
  name: 'FavoritesView',
  data(){
    return {
      loadContent: true
    }
  },
  components: {
    TheLoader,
    BookCartBar,
    ButtonBrown
  },
  computed: {
    ...mapGetters([
      'cartBooksList',
      'cartSize'
    ]),
    totalPrice(){
      let totalPrice = 0;
      this.cartBooksList.forEach((book:booksType) => {
        totalPrice += (book.discount) ? Math.ceil(book.price * book.quantityInCart * (100 - book.discount)/100) : book.price * book.quantityInCart;
      });
      return totalPrice.toLocaleString('de-DE');
    },
    emptyCart(){
      return !this.cartSize;
    }
  },
  watch: {
    cartBooksList(){
      this.loadContent = false;
    },
  },
  methods: {
    ...mapActions([
      'getCartBooks'
    ])
  },
  mounted(){
    if(!this.cartSize){
      this.loadContent = false;
    } else {
      this.getCartBooks();
    }
  }
});
